body {
  margin: 0;
  padding: 0;
  font-family: Noto Sans, system-ui;
}
.message {
    width: 100%;
    margin-bottom: 1vh;
}

.messageInfo {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: baseline;
            align-items: baseline;
    margin-bottom: 2px;
}

.messageAuthor {
    font-weight: bold;
    font-size: calc(14px + (18 - 14) * ((100vh - 600px) / (1080 - 600)));
    margin-right: 10px;
    color: rgb(86, 129, 148);
}

.messageTime {
    font-size: calc(8px + (11 - 8) * ((100vh - 600px) / (1080 - 600)));
    opacity: 0.7;
    vertical-align: baseline;
}

.messageText {
    font-size: calc(9px + (13 - 9) * ((100vh - 600px) / (1080 - 600)));
    overflow-wrap: break-word;
}

.messageText a {
    color: rgb(189, 212, 221);
}
.chatInputBox {
    width: 100%;
    height: 5%;
    display: -webkit-flex;
    display: flex;
    background-color: rgb(17, 19, 20);
}

.chatInput {
    height: 100%;
    width: 90%;
    box-sizing: border-box;
    border: none;
    background-color: rgb(18, 18, 18);
    font-size: calc(10px + (14 - 10) * ((100vh - 600px) / (1080 - 600)));
    padding: 0 8px;
    outline: none;
    color: white;
}

.chatSend {
    height: 100%;
    width: 10%;
    border: none;
    background-color: rgb(18, 18, 18);
    font-size: calc(10px + (14 - 10) * ((100vh - 600px) / (1080 - 600)));
    color: white;
}

.chatSend:hover {
    background-color: #1C5F97;
    cursor: pointer;
}

.disconnectMobile {
    height: 100%;
    width: 10%;
    border: none;
    background-color: red;
}

.disconnectMobile img {
    height: 20px;
}
.chatContainer {
    background-color: rgba(6, 28, 46, 0.75);
    width: 64vh;
    height: 80vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    box-shadow: 0px 0px 50px 10px rgb(0, 0, 0);
}

@media only screen and (max-width: 500px) {
    .chatContainer {
        width: 100vw;
        height: 100vh;
    }
}

.innerChatContainer {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    z-index:  1;
}

.chatTextBox {
    width: 100% - 30px;
    height: 95%;
    padding: 1.8vh 1.2vh;
    color: rgb(189, 212, 221);
    overflow-y: auto;
}

#scroll::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #000000;
}

#scroll::-webkit-scrollbar
{
	background-color: #000000;
}

#scroll::-webkit-scrollbar-thumb
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #1C5F97;
}

.userTyping {
    font-size: calc(8px + (11 - 8) * ((100vh - 600px) / (1080 - 600)));
    opacity: 0.7;
    margin: 0.5vh 0;
    color: rgb(189, 212, 221);
    padding-left: 1.2vh;
}

.connectionMsg {
    width: 100%;
    margin-bottom: 1vh;
}

.connectionTxt {
    font-size: calc(9px + (13 - 9) * ((100vh - 600px) / (1080 - 600)));
    overflow-wrap: break-word;
}

#joined {
    color: rgb(0, 255, 115);
}

#left {
    color: rgb(255, 88, 130);
}
.user {
    width: 6vh;
    height: 6vh;
    margin: 10px;
    border: 2px solid rgb(33, 112, 177);
    border-radius: 500%;
    box-shadow: 0 0 20px 2px rgb(33, 112, 177);
    overflow: hidden;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    object-fit: none;
  }
  
  .user:hover {
    border: 2px solid rgb(158, 0, 197);
    box-shadow: 0 0 30px 3px rgb(158, 0, 197);
  }
  
  .userLogo {
    width: 150%;
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  .nameHover {
    display: none;
    position: absolute;
    left: auto;
    top: auto;
    margin-top: -3.5vh;
    margin-left: 2vh;
    padding: 5px 10px;
    background-color: black;
    border: 2px solid rgb(158, 0, 197);
    box-shadow: 0 0 20px 2px rgb(158, 0, 197);
    color: rgb(158, 0, 197);
    border-radius: 5px;
    font-size: 1.2vh;
    text-align: center;
    z-index: 100;
  }

  .user:hover > .nameHover {
    display: block;
  } 
.connectBar {
    width: 100%;
    height: 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgb(17, 19, 20);
    font-size: calc(10px + (14 - 10) * ((100vh - 600px) / (1080 - 600)));
    color: white;
}

.connectBar:hover {
    background-color: #1C5F97;
    cursor: pointer;
}

.wrapModal {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 10;
}

.connModal {
    width: 350px;
    height: 500px;
    background: -webkit-linear-gradient(rgb(39, 39, 39), rgb(31, 31, 31));
    background: linear-gradient(rgb(39, 39, 39), rgb(31, 31, 31));
    z-index: 11;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 10px 1px #1C5F97;
}

.loginForm {
    width: 100%;
    height: 80%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    /* justify-content: space-evenly; */
}

.loginForm input {
    color: white;
    background-color: #141414;
    width: 60%;
    height: 40px;
    border: none;
    padding: 0 10px;
    font-size: 16px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
}

.labelWrap {
    width: 60%;
    padding: 15px;
    padding-left: 0;
    margin-top: 20px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    color: red;
    font-size: 10px;
}

.formLabel {
    color: white;
    font-size: 16px;
    margin-right: 10px;
}

.formLabel > :first-child {
    margin-top: 30px;
}

.submitBtn {
    width: 230px;
    height: 40px;
    margin-top: 50px;
    background-color: #1C5F97;
    color: white;
    font-size: 16px;
    border: none;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
}

.submitBtn:hover {
    cursor: pointer;
    opacity: 0.7;
}

.connMenu {
    display: -webkit-flex;
    display: flex;
}

.connMenuItem {
    width: 50%;
    padding: 15px;
    /* background-color: rgb(51, 51, 51); */
    color: white;
    text-align: center;
}

.connMenuItem:hover {
    cursor: pointer;
    background-color: black;
}
.appContainer {
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-image: url(/static/media/stars_bg.b52b5b29.jpg);
}

.dummyWidth {
  margin-right: 3vh;
  width: 13vh;
}

.scrollWrapper {
  margin-left: 3vh;
  overflow: hidden;
  width: 15vh;
}

.userContainer {
  width: 13vh;
  height: 80vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  overflow: auto;
}

.userContainer::-webkit-scrollbar { width: 0 !important }
.userContainer { -ms-overflow-style: none; }

.disconnect {
  width: 6vh;
  height: 6vh;
  margin: 10px;
  border: 2px solid rgb(0, 0, 0);
  background-color: red;
  border-radius: 500%;
  position: relative;
  overflow: hidden;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  object-fit: none;
}

.disconnect:hover {
  box-shadow: 0 0 20px 2px red;
  border: 2px solid red;
  cursor: pointer;
}

.disconnect img {
  width: 100%;
}
